* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}




.wrapper {
  display: flex;
  flex-wrap: nowrap;
  height: auto;
  width: 100%;
  justify-content: center;
  align-items: center;

}

.wrapper .main {
  font-family: "Inter", sans-serif;
  letter-spacing: 0.2rem;
  height: 80svh;
  width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-align: left;

}

.wrapper .robot{
  width: 60%;
}



.wrapper .main .txtbottom{
  text-align: left;
  font-size: 17px;
  letter-spacing: 2.5px;
  font-weight: 400;
}

.wrapper .main .txttop{
  height: auto;
  text-align: left;
  font-weight: 600;
  font-size: 50px;
}

.txttopp{
  text-align: center;
}


.wrapper .main h1 {
  /* font-size: 40px; */
  letter-spacing: -2px;
  overflow: hidden;
  color: #0d0d0d;
  padding-left: 100px;
}

.wrapper .main h2 {

  overflow: hidden;

}



.wrapper .main .txttopp{
  overflow: hidden;
  padding-left: 100px;
}


.wrapper .main h1 span {
  display: inline-block;
  transform: translateY(200%);
  line-height: 1;
}


.dropping-texts {
  height:50px;
  overflow:hidden;
}

  @media only screen and (max-width: 600px) {

  .wrapper {
    flex-wrap: wrap;
    gap: 0px;
    padding-top: 60px;
  }

  .wrapper .main {
    display: flex;
    margin-top: -60px;
    height: 55svh;
    width: 600px;
    justify-content: center;
    align-items:center;
    text-align: center;
  }


  

  .wrapper  .robot{
    padding-top: -10px;
    width: 100%;
    height: 65svh;

  }

  .wrapper  .txt{
    padding-top: 60px;
    height: 40svh;

  }
  .wrapper .main .txttop{

    font-size: 10vw;

  }


  .wrapper .main .txtbottom {
    justify-content: center;
    align-items: center;
    text-align: center;
    letter-spacing: 1.1px;
    font-size: 4vw;
  }

  
  .wrapper .main h1 {
    letter-spacing: -2px;
    padding-left: 0;
  }
  


}



@media only screen and (min-width: 610px) and (max-width: 1025px)  {

  .wrapper {
    flex-wrap: wrap;
    gap: 0px;
    padding-top: 0;
  }

  .wrapper .main {
    display: flex;
    margin-top: 0;
    height: 30svh;
    justify-content: center;
    align-items:center;
    text-align: center;

  }

  .wrapper  .robot{
    width: 100%;
    padding-top: -10px;
    height: 65svh;

  }

  .wrapper  .txt{
    padding-top: 0;

  }

  .wrapper .main .txttop{
    font-size: 6vw;
    letter-spacing: 2.5px;
  }

  

  .wrapper .main .txtbottom {
    letter-spacing: 1.1px;
    font-size: 3vw;
 
  }
  .wrapper .main h1 {
    letter-spacing: -2px;
    padding-left: 0;
  }
  


}



